*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  color: #333; }

.App--header {
  height: 80px;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  background-color: #4285f4; }

.h1 {
  font-size: 32px;
  margin: 0;
  padding: 24px 0 24px 24px;
  color: #fff;
  font-weight: 300; }

.App--h2 {
  margin-top: 16px;
  text-align: center; }

.App--div--darksky {
  text-align: center;
  margin: 64px 0 32px; }
  .App--div--darksky a {
    color: #4285f4; }

.Search {
  margin: 32px;
  height: 32px;
  text-align: center; }

.Search--input {
  padding: 8px 0 0;
  font-size: 24px;
  outline: none;
  text-align: center;
  width: 320px;
  border: none;
  border-bottom: 2px solid #dfe1e4; }
  .Search--input::-webkit-input-placeholder {
    font-size: 20px;
    color: #dfe1e4; }
  .Search--input::-ms-input-placeholder {
    font-size: 20px;
    color: #dfe1e4; }
  .Search--input::placeholder {
    font-size: 20px;
    color: #dfe1e4; }
  .Search--input:focus {
    border-bottom: 2px solid #4285f4; }

.icon, .clear-day, .clear-night, .cloudy, .fog, .partly-cloudy-day, .partly-cloudy-night, .rain, .sleet, .snow, .wind {
  height: 80px;
  width: 80px;
  display: inline-block;
  vertical-align: middle; }

.clear-day {
  background-image: url(/weekly-weather/static/media/clear-day.4b9869a6.svg); }

.clear-night {
  background-image: url(/weekly-weather/static/media/clear-night.f86c33c4.svg); }

.cloudy {
  background-image: url(/weekly-weather/static/media/cloudy.2d0410c5.svg); }

.fog {
  background-image: url(/weekly-weather/static/media/fog.c356784f.svg); }

.partly-cloudy-day {
  background-image: url(/weekly-weather/static/media/partly-cloudy-day.3237f7da.svg); }

.partly-cloudy-night {
  background-image: url(/weekly-weather/static/media/partly-cloudy-night.abda2f64.svg); }

.rain {
  background-image: url(/weekly-weather/static/media/rain.e13be320.svg); }

.sleet {
  background-image: url(/weekly-weather/static/media/sleet.8291f353.svg); }

.snow {
  background-image: url(/weekly-weather/static/media/snow.1a2bbf7d.svg); }

.wind {
  background-image: url(/weekly-weather/static/media/wind.8f977e0c.svg); }

.WeatherContainer--width, .article, .section, .Graph {
  margin: auto;
  width: 90%; }

.article {
  text-align: center;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.article--header {
  grid-column: 1/4;
  margin-bottom: 16px; }

.article--div {
  margin: auto;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(3, 1fr); }

.header--span--feels {
  margin-right: 5%; }

.header--span--precip {
  margin-left: 5%; }

.p {
  margin: 4px; }

.section--h3 {
  text-align: center;
  margin: 16px; }

.ForecastBar {
  margin-bottom: 16px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  border-radius: 36px; }

.ForecastBar--bar-container {
  width: 100%;
  height: 64px;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  grid-template-columns: repeat(3, 48px) 1fr repeat(2, 48px);
  font-size: 20px;
  vertical-align: middle;
  padding: 8px 16px; }

.small-icon {
  width: 48px;
  height: 48px; }

.bar-container {
  height: 20px;
  margin-bottom: 6px;
  grid-column: 4/5; }

.bar {
  height: 17px;
  background-color: #333333;
  border-radius: 10px;
  display: inline-block;
  vertical-align: middle;
  position: relative; }

.high {
  vertical-align: middle;
  position: absolute;
  right: -42px;
  top: -3px; }

.ForecastBar--button {
  border-radius: 24px;
  background-color: #4285f4;
  grid-column: 6/7;
  height: 36px;
  outline: none;
  border: none;
  color: #fff;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); }

.low {
  position: absolute;
  left: -35px;
  top: -3px; }

.day {
  margin-left: 8px; }

.article--expanded {
  width: 100%;
  padding-bottom: 16px; }

.Graph {
  height: 450px;
  margin: 40px auto 60px; }

.h3 {
  text-align: center; }

